import { registerBlockType } from '@wordpress/blocks';
import './style.scss';

import metadata from './block.json';

/**
 * Internal dependencies
 */
import saveDep from './deprecated';
import Edit from './edit';
import Save from './save';

/**
 * Block Registration
 */

registerBlockType(metadata, {
	icon: {
		src: 'minus',
		foreground: '#77b5f7',
	},
	usesContext: [
		'aagb/accordion-progressBarDirection',
		'aagb/accordion-duration',
		'aagb/accordion-progressBarActiveColor',
		'aagb/accordion-progressBarBgColor',
		'aagb/accordion-autoPlay',
		'aagb/accordion-QaStyle',
		'aagb/accordion-faqSchema',
		'aagb/accordion-step',
		'aagb/accordion-stepText',
		'aagb/accordion-stepCmpltText',
		'aagb/accordion-checkList',
		'aagb/accordion-anchorLinksShow',
		'aagb/accordion-buttonShow',
		'aagb/accordion-readMoreText',
		'aagb/accordion-border',
		'aagb/accordion-margins',
		'aagb/accordion-paddings',
		'aagb/accordion-borderRadius',
		'aagb/accordion-headingColor',
		'aagb/accordion-showIcon',
		'aagb/accordion-iconColor',
		'aagb/accordion-iconBackground',
		'aagb/accordion-headerBg',
		'aagb/accordion-bodyBg',
		'aagb/accordion-qIconText',
		'aagb/accordion-qIconColor',
		'aagb/accordion-qIconBg',
		'aagb/accordion-aIconText',
		'aagb/accordion-aIconColor',
		'aagb/accordion-aIconBg',
		'aagb/accordion-contentCount',
		'aagb/accordion-subheadingColor',
		'aagb/accordion-headingBorder',
		'aagb/accordion-bodyBorder',
		'aagb/accordion-categoryList',
		'aagb/accordion-labelsGlobalTextColor',
		'aagb/accordion-labelsGlobalBgColor',
		'aagb/accordion-accessibilityOn',
		'aagb/accordion-focusOutlineColor',
		'aagb/accordion-isFeatureImg',
		'aagb/accordion-autoNumbering',
		'aagb/accordion-autoNumberingColor',
        'aagb/accordion-progressBarOn',
	],

	edit: Edit,
	save: Save,
	deprecated: [
		{
			attributes: {
				QaStyle: {
					type: 'boolean',
					default: false,
				},
				singleAcdId: {
					type: 'string',
				},
				border: {
					type: 'object',
					default: {
						width: '1px',
						color: '#bcb6b638',
						style: 'solid',
					},
				},
				headingBorder: {
					type: 'object',
					default: {
						color: null,
						style: 'solid',
						width: '0px',
					},
				},
				bodyBorder: {
					type: 'object',
					default: {
						top: {
							color: '#bcb6b638',
							style: 'solid',
							width: '1px',
						},
					},
				},
				paddings: {
					type: 'object',
					default: {
						top: '',
						right: '',
						left: '',
						bottom: '',
					},
				},
				margins: {
					type: 'object',
					default: {
						top: '0px',
						right: '',
						left: '',
						bottom: '15px',
					},
				},
				borderRadius: {
					type: 'number',
				},
				heading: {
					type: 'string',
					default: 'Accordion Heading',
				},
				subheading: {
					type: 'string',
					default: '',
				},
				subheadingColor: {
					type: 'string',
				},
				headingTag: {
					type: 'string',
				},
				headingColor: {
					type: 'string',
				},
				headerBg: {
					type: 'string',
					default: '#bcb6b638',
				},
				headingIconImageUrl: {
					type: 'string',
					default: '',
				},
				headingIconAlt: {
					type: 'string',
					default: '',
				},
				showHeadingIcon: {
					type: 'boolean',
					default: false,
				},
				showIcon: {
					type: 'boolean',
					default: true,
				},
				iconClass: {
					type: 'string',
					default: 'plus-alt2',
				},
				iconPosition: {
					type: 'string',
					default: 'aab_right_icon',
				},
				iconColor: {
					type: 'string',
				},
				iconBackground: {
					type: 'string',
				},
				iconFontSize: {
					type: 'number',
				},
				bodyBg: {
					type: 'string',
				},
				makeActive: {
					type: 'boolean',
					default: false,
				},
				uniqueId: {
					type: 'string',
				},
				buttonShow: {
					type: 'boolean',
					default: false,
				},
				anchorLinkShow: {
					type: 'boolean',
					default: false,
				},
				anchorPosition: {
					type: 'string',
					default: 'aagb_right_link',
				},
				contentCount: {
					type: 'number',
					default: 300,
				},
				readMoreText: {
					type: 'string',
				},
				qIconText: {
					type: 'string',
					default: 'Q',
				},
				qIconColor: {
					type: 'string',
					default: '#fff',
				},
				qIconBg: {
					type: 'string',
					default: '#505050',
				},
				aIconText: {
					type: 'string',
					default: 'A',
				},
				aIconColor: {
					type: 'string',
					default: '#fff',
				},
				aIconBg: {
					type: 'string',
					default: '#f5a623',
				},
				faqSchema: {
					type: 'boolean',
				},
				step: {
					type: 'boolean',
				},
				stepText: {
					type: 'string',
				},
				stepCmpltText: {
					type: 'string',
					default: 'Congratulations, you are done!',
				},
				checkList: {
					type: 'boolean',
				},
				button_show: {
					type: 'boolean',
				},
				selectedCategories: {
					type: 'array',
					default: [],
				},
				enableLabels: {
					type: 'boolean',
					default: false,
				},
				labels: {
					type: 'array',
					default: [],
				},
				labelsTextColor: {
					type: 'string',
					default: '',
				},
				labelsBgColor: {
					type: 'string',
					default: '',
				},
				labelsGlobalTextColor: {
					type: 'string',
					default: '#1570EC',
				},
				labelsGlobalBgColor: {
					type: 'string',
					default: '#c1d8f7',
				},
				progressBarActiveColor: {
					type: 'string',
					default: '#0866ff',
				},
				progressBarBgColor: {
					type: 'string',
					default: '#e0e0e0',
				},
				autoPlay: {
					type: 'boolean',
					default: false,
				},
				duration: {
					type: 'number',
					default: 3000,
				},
				progressBarDirection: {
					type: 'string',
					default: 'horizontal',
				},
				featureImageUrl: {
					type: 'string',
					default: '',
				},
				featureImageAlt: {
					type: 'string',
					default: '',
				},
				isFeatureImg: {
					type: 'boolean',
					default: false,
				},
				autoNumbering: {
					type: 'boolean',
					default: false,
				},
				autoNumberingColor: {
					type: 'string',
				},
			},
			migrate: (attributes) => {
				if (attributes.accessibilityOn === undefined)
					attributes.accessibilityOn = true;
				if (attributes.focusOutlineColor === undefined)
					attributes.focusOutlineColor = '#C2DBFE';

				return { ...attributes };
			},
			save: saveDep,
		},
	],
});
