import { RichText as DefaultRichText } from "@wordpress/block-editor";
import { __ } from "@wordpress/i18n";
import CompleteIcon from "../../components/icons/complete";
import { createAttrGetter } from "../../utils/global-settings";

const iconClassesMap = {
  "plus-alt": "dismiss",
  "plus-alt2": "minus",
  "arrow-down": "arrow-up",
  "arrow-down-alt2": "arrow-up-alt2",
  "insert": "remove",
}

export default function AccordionHead({ isEditor, attributes, setAttributes }){ 
  const RichText = isEditor ? DefaultRichText : DefaultRichText.Content;
  const aab_premium = aagb_local_object.licensing;
  const {
    QaStyle,
    makeActive,
    heading,
    showIcon,
    iconClass,
    iconPosition,
    anchorPosition,
    step,
    checkList,
    headingIconImageUrl,
    headingIconAlt,
    showHeadingIcon,
    subheading,
    enableLabels,
    labels,
    labelsTextColor,
    labelsBgColor,
    labelsGlobalTextColor,
    labelsGlobalBgColor,
    accessibilityOn,
    autoNumbering,
    
  } = attributes;
  const getAttrDeep = createAttrGetter(attributes);
  const anchorLinkShow = getAttrDeep('anchorLinkShow') ?? false;
  const headingTag = getAttrDeep('headingTag') ?? 'h5';
  const aIconText = getAttrDeep('aIconText') ?? 'A';
  const qIconText = getAttrDeep('qIconText') ?? 'Q';
  const currentIconClass = makeActive ? iconClassesMap[iconClass] : iconClass;
  let subheadingPlaceholder = aab_premium ? "Write some subheading" : "Subheading Available on Pro";
  if(!isEditor) subheadingPlaceholder = "";

  return (
		<div
			className={`aagb__accordion_head ${iconPosition} ${subheading && aab_premium ? 'hasSubHeading' : ''} ${makeActive ? 'aagb__accordion_active' : ''}`}
			data-active={makeActive}
		>
			<div
				className={`aagb__accordion_heading ${iconPosition} ${anchorPosition || ''}`}
			>
				{aab_premium && checkList && (
					<label className="checklist-label">
						<span />
					</label>
				)}
				{showHeadingIcon &&
					headingIconImageUrl &&
					aab_premium &&
					!QaStyle && (
						<div className="heading-icon">
							<img
								src={headingIconImageUrl}
								alt={__(
									headingIconAlt || 'Heading Icon',
									'advanced-accordion-block'
								)}
							/>
						</div>
					)}

				{QaStyle && aab_premium && (
					<div className="icon-container">
						<div className="icon-q">{qIconText}</div>
						<div className="icon-a">{aIconText}</div>
					</div>
				)}

				<div className="head_content_wrapper">
					<div className="title_wrapper">
						<RichText
							className="aagb__accordion_title"
							tagName={headingTag}
                             value={
                                autoNumbering
                                    ? `<span class="aagb__accordion_number"></span> ${heading}`
                                    : heading
                            }
							onChange={(heading) => setAttributes({ heading })}
						/>
						{aab_premium && enableLabels && labels?.length ? (
							<div className="aagb__labels_container">
								{labels.map((label) => {
									return (
										<small
											className="label_name"
											style={{
												color:
													labelsTextColor ||
													labelsGlobalTextColor,
												backgroundColor:
													labelsBgColor ||
													labelsGlobalBgColor,
											}}
										>
											{label}
										</small>
									);
								})}
							</div>
						) : null}
						{anchorLinkShow && aab_premium && !step && isEditor && (
							<a
								tabIndex={accessibilityOn ? 0 : -1}
								className="anchorjs-link"
								href="#"
							>
								<i className="dashicons dashicons-admin-links"></i>
							</a>
						)}
					</div>

					{aab_premium &&
						(isEditor || (!isEditor && subheading !== '')) && (
							<RichText
								className="aagb__accordion_subheading"
								tagName="p"
								placeholder={subheadingPlaceholder}
								value={aab_premium ? subheading : ''}
								onChange={(value) => {
									setAttributes({ subheading: value });
								}}
								onFocus={(e) => {
									if (!aab_premium) {
										e.target.blur(); // Prevent focus if aab_premium is false
									}
								}}
							/>
						)}
				</div>
			</div>
			{showIcon ? (
				<div className={`aagb__accordion_icon`}>
					<div className="aagb__icon_dashicons_box">
						{step && (
							<span className="complete-sign">
								<CompleteIcon />
							</span>
						)}
						<span
							className={`aagb__icon dashicons dashicons-${currentIconClass}`}
						/>
					</div>
				</div>
			) : (
				step && (
					<span className="complete-sign">
						<CompleteIcon />
					</span>
				)
			)}
		</div>
  );
}