import { isPro } from '../../utils/constants';

export function ProgressBar() {}

ProgressBar.Horizontal = (props) => {
	const { autoPlay, progressBarDirection, progressBarOn } = props.attributes;

	if (!isPro || !autoPlay || progressBarDirection !== 'horizontal' || !progressBarOn)
		return null;

	return (
		<div className="aab-progress-bar-container aab-progress-bar-horizontal">
			<div className="aab-progress-bar"></div>
		</div>
	);
};

ProgressBar.Vertical = (props) => {
	const { autoPlay, progressBarDirection } = props.attributes;

	if (!isPro || !autoPlay || progressBarDirection !== 'vertical') return null;

	return (
		<div className="aab-progress-bar-container aab-progress-bar-vertical">
			<div className="aab-progress-bar"></div>
		</div>
	);
};
