import { InspectorControls } from '@wordpress/block-editor';

import '../editor.scss';
import AccordionHead from './settings/accordion-head';
import AccordionIconPanel from './settings/accordion-icon';
import AccordionIdPanel from './settings/accordion-id';
import AccordionStatusPanel from './settings/accordion-status';
import AnchorLinkPanel from './settings/anchor-link';
import CategoryFilterPanel from './settings/category-filter';
import FeatureImageSettings from './settings/feature-image';
import LabelsPanel from './settings/labels-panel';

function Settings(props) {
	const { isFeatureImg } = props.attributes;
	return (
		<InspectorControls>
			<AccordionIdPanel {...props} />
			<LabelsPanel {...props} />
			<CategoryFilterPanel {...props} />
			<AccordionStatusPanel {...props} />
			<AccordionHead {...props} />
			<AnchorLinkPanel {...props} />
			<AccordionIconPanel {...props} />
			<FeatureImageSettings {...props} />
		</InspectorControls>
	);
}

function Styles(props) {
	return <></>;
}

const Editor = { Settings, Styles };
export default Editor;
