import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { Button, PanelBody } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { isPro } from '../../../utils/constants';

export default function FeatureImageSettings({ attributes, setAttributes }) {
	const { featureImageUrl, featureImageAlt,isFeatureImg } = attributes;

    if (!isPro || !isFeatureImg) {
		return null;
	}

	return (
		<PanelBody
			title={__('Feature Image', 'advanced-accordion-block')}
			initialOpen={false}
			className={isPro ? '' : 'aab-pro-element'}
		>
			<MediaUploadCheck>
				<MediaUpload
					onSelect={(media) => {
						setAttributes({
							featureImageUrl: media.url,
							featureImageAlt: media.alt,
						});
					}}
					allowedTypes={['image']}
					render={({ open }) => (
						<Button
							onClick={open}
							variant="secondary"
							icon="format-image"
						>
							{featureImageUrl
								? __(
										'Change Image',
										'advanced-accordion-block'
									)
								: __(
										'Add Image',
										'advanced-accordion-block'
									)}
						</Button>
					)}
				/>
			</MediaUploadCheck>

			{featureImageUrl && (
				<img
					src={featureImageUrl}
					alt={
						featureImageAlt ||
						__('Feature Image', 'advanced-accordion-block')
					}
					style={{ maxWidth: '100%', height: 'auto' }}
				/>
			)}
		</PanelBody>
	);
}
