/* eslint-disable react/jsx-no-target-blank */
/* eslint-disable no-shadow */
/* eslint-disable @wordpress/no-unsafe-wp-apis */
import { useEffect } from '@wordpress/element';

import disableQAButton, { cleanupQAButtonObserver } from '../utils/disable-qa-button';
import { loadGlobalSettings } from '../utils/global-settings';
import Editor from './editor';
import Output from './output';

const Edit = (props) => {
	const { setAttributes, context } = props;

	// Check if aab_premium is true
	const aab_premium = aagb_local_object.licensing;

	const hasDirection = context['aagb/accordion-progressBarDirection'];
	const hasDuration = context['aagb/accordion-duration'];
	const hasProgressBarActiveColor =
		context['aagb/accordion-progressBarActiveColor'];
	const hasProgressBarBgColor = context['aagb/accordion-progressBarBgColor'];
	const hasAutoPlay = context['aagb/accordion-autoPlay'];
	const hasQaStyle = context['aagb/accordion-QaStyle'];
	const hasFaqSchema = context['aagb/accordion-faqSchema'];
	const hasStep = context['aagb/accordion-step'];
	const hasStepText = context['aagb/accordion-stepText'];
	const hasStepCmpltText = context['aagb/accordion-stepCmpltText'];
	const hasCheckList = context['aagb/accordion-checkList'];

	const buttonShowContext = context['aagb/accordion-buttonShow'];
	const readMoreTxt = context['aagb/accordion-readMoreText'];
	const hasBorder = context['aagb/accordion-border'];
	const hasMargins = context['aagb/accordion-margins'];
	const hasPaddings = context['aagb/accordion-paddings'];
	const hasBorderRadius = context['aagb/accordion-borderRadius'];
	const hasHeadingColor = context['aagb/accordion-headingColor'];
	const hasShowIcon = context['aagb/accordion-showIcon'];
	const hasIconColor = context['aagb/accordion-iconColor'];
	const hasIconBackground = context['aagb/accordion-iconBackground'];
	const hasHeaderBg = context['aagb/accordion-headerBg'];
	const hasBodyBg = context['aagb/accordion-bodyBg'];
	const hasQIconText = context['aagb/accordion-qIconText'];
	const hasQIconColor = context['aagb/accordion-qIconColor'];
	const hasQIconBg = context['aagb/accordion-qIconBg'];
	const hasAIconText = context['aagb/accordion-aIconText'];
	const hasAIconColor = context['aagb/accordion-aIconColor'];
	const hasAIconBg = context['aagb/accordion-aIconBg'];
	const hasContentCount = context['aagb/accordion-contentCount'];
	const hasSubheadingColor = context['aagb/accordion-subheadingColor'];
	const hasHeadingBorder = context['aagb/accordion-headingBorder'];
	const hasBodyBorder = context['aagb/accordion-bodyBorder'];
	const hasCategoryList = context['aagb/accordion-categoryList'];
	const hasLabelsGlobalTextColor =
		context['aagb/accordion-labelsGlobalTextColor'];
	const hasLabelsGlobalBgColor =
		context['aagb/accordion-labelsGlobalBgColor'];
	const hasAccessibilityOn = context['aagb/accordion-accessibilityOn'];
	const hasFocusOutlineColor = context['aagb/accordion-focusOutlineColor'];
	const hasAnchorLinksShow = context['aagb/accordion-anchorLinksShow'];
	const hasFeatureImg = context['aagb/accordion-isFeatureImg'];
	const hasAutoNumbering = context['aagb/accordion-autoNumbering'];
	const hasAutoNumberingColor = context['aagb/accordion-autoNumberingColor'];
    const hasProgressBarOn = context['aagb/accordion-progressBarOn'];

	useEffect(() => {
		setAttributes({ progressBarDirection: hasDirection });
	}, [hasDirection]);

	useEffect(() => {
		setAttributes({ duration: hasDuration });
	}, [hasDuration]);

	useEffect(() => {
		setAttributes({ autoPlay: hasAutoPlay });
	}, [hasAutoPlay]);

	useEffect(() => {
		setAttributes({ progressBarActiveColor: hasProgressBarActiveColor });
	}, [hasProgressBarActiveColor]);

	useEffect(() => {
		setAttributes({ progressBarBgColor: hasProgressBarBgColor });
	}, [hasProgressBarBgColor]);

	useEffect(() => {
		setAttributes({ anchorLinkShow: hasAnchorLinksShow });
	}, [hasAnchorLinksShow]);

	useEffect(() => {
		setAttributes({ QaStyle: hasQaStyle });
	}, [hasQaStyle]);

	useEffect(() => {
		setAttributes({ faqSchema: hasFaqSchema });
	}, [hasFaqSchema]);

	useEffect(() => {
		setAttributes({ step: hasStep });
	}, [hasStep]);

	useEffect(() => {
		setAttributes({ stepText: hasStepText });
	}, [hasStepText]);

	useEffect(() => {
		setAttributes({ stepCmpltText: hasStepCmpltText });
	}, [hasStepCmpltText]);

	useEffect(() => {
		setAttributes({ checkList: hasCheckList });
	}, [hasCheckList]);

	useEffect(() => {
		setAttributes({ button_show: buttonShowContext });
	}, [buttonShowContext]);
	useEffect(() => {
		setAttributes({ readMoreText: readMoreTxt });
	}, [readMoreTxt]);
	useEffect(() => {
		setAttributes({ border: hasBorder });
	}, [hasBorder]);
	useEffect(() => {
		setAttributes({ margins: hasMargins });
	}, [hasMargins]);
	useEffect(() => {
		setAttributes({ paddings: hasPaddings });
	}, [hasPaddings]);
	useEffect(() => {
		setAttributes({ borderRadius: hasBorderRadius });
	}, [hasBorderRadius]);
	useEffect(() => {
		setAttributes({ headingColor: hasHeadingColor });
	}, [hasHeadingColor]);
	useEffect(() => {
		setAttributes({ showIcon: hasShowIcon });
	}, [hasShowIcon]);
	useEffect(() => {
		setAttributes({ iconColor: hasIconColor });
	}, [hasIconColor]);
	useEffect(() => {
		setAttributes({ iconBackground: hasIconBackground });
	}, [hasIconBackground]);
	useEffect(() => {
		setAttributes({ headerBg: hasHeaderBg });
	}, [hasHeaderBg]);
	useEffect(() => {
		setAttributes({ bodyBg: hasBodyBg });
	}, [hasBodyBg]);
	useEffect(() => {
		setAttributes({ qIconText: hasQIconText });
	}, [hasQIconText]);
	useEffect(() => {
		setAttributes({ qIconColor: hasQIconColor });
	}, [hasQIconColor]);
	useEffect(() => {
		setAttributes({ qIconBg: hasQIconBg });
	}, [hasQIconBg]);
	useEffect(() => {
		setAttributes({ aIconText: hasAIconText });
	}, [hasAIconText]);
	useEffect(() => {
		setAttributes({ aIconColor: hasAIconColor });
	}, [hasAIconColor]);
	useEffect(() => {
		setAttributes({ aIconBg: hasAIconBg });
	}, [hasAIconBg]);
	useEffect(() => {
		setAttributes({ contentCount: hasContentCount });
	}, [hasContentCount]);
	useEffect(() => {
		setAttributes({ subheadingColor: hasSubheadingColor });
	}, [hasSubheadingColor]);
	useEffect(() => {
		setAttributes({ headingBorder: hasHeadingBorder });
	}, [hasHeadingBorder]);
	useEffect(() => {
		setAttributes({ bodyBorder: hasBodyBorder });
	}, [hasBodyBorder]);
	useEffect(() => {
		setAttributes({ categoryList: hasCategoryList });
	}, [hasCategoryList]);
	useEffect(() => {
		setAttributes({ labelsGlobalTextColor: hasLabelsGlobalTextColor });
	}, [hasLabelsGlobalTextColor]);
	useEffect(() => {
		setAttributes({ labelsGlobalBgColor: hasLabelsGlobalBgColor });
	}, [hasLabelsGlobalBgColor]);
	useEffect(() => {
		setAttributes({ accessibilityOn: hasAccessibilityOn });
	}, [hasAccessibilityOn]);
	useEffect(() => {
		setAttributes({ focusOutlineColor: hasFocusOutlineColor });
	}, [hasFocusOutlineColor]);

	useEffect(() => {
		setAttributes({ autoNumbering: hasAutoNumbering });
	}, [hasAutoNumbering]);
	useEffect(() => {
		setAttributes({ autoNumberingColor: hasAutoNumberingColor });
	}, [hasAutoNumberingColor]);

    useEffect(() => {
        setAttributes({ progressBarOn: hasProgressBarOn });
    }, [hasProgressBarOn]);

	useEffect(() => {
		const fetchGlobalSettings = async () => {
			const settings = await loadGlobalSettings(); // Fetch global settings

			setAttributes({ defaultStyles: settings });
		};
		fetchGlobalSettings(); // Fetch settings when the component mounts
	}, []);

	useEffect(() => {
		setAttributes({ isFeatureImg: hasFeatureImg });
	}, [hasFeatureImg]);

	// Only call disableQAButton once when component mounts if premium is false
	useEffect(() => {
		if (!aab_premium) {
			disableQAButton();
		}

		// Cleanup on unmount
		return () => {
			if (!aab_premium) {
				cleanupQAButtonObserver();
			}
		};
	}, [aab_premium]);

	return (
		<>
			<Editor.Settings {...props} />
			<Editor.Styles {...props} />
			<Output isEditor {...props} />
		</>
	);
};

export default Edit;
