import { useBlockProps as useDefaultBlockProps } from '@wordpress/block-editor';
import { createAttrGetter } from '../../utils/global-settings';
import AccordionBody from './accordion-body';
import AccordionHead from './accordion-head';
import { ProgressBar } from './progress-bar';

export default function Output(props) {
	const useBlockProps = props.isEditor
		? useDefaultBlockProps
		: useDefaultBlockProps.save;

	const {
		singleAcdId,
		autoPlay,
		makeActive,
		QaStyle,
		step,
		checkList,
		selectedCategories,
		accessibilityOn,
		duration,
		progressBarDirection,
		featureImageUrl,
        autoNumbering,
        progressBarOn
	} = props.attributes;

	const getAttrDeep = createAttrGetter(props.attributes);
	const border = getAttrDeep('border') ?? {};

	const categoryClassList = (selectedCategories || []).map((item) => {
		return (
			'cat_' +
			item
				.toLowerCase()
				.replace(/\s+/g, '-') // Replace spaces with dashes
				.replace(/&/g, 'and') // Replace '&' with 'and'
				.replace(/%/g, 'percent') // Replace '%' with 'percent'
				.replace(/[^a-z0-9-]/g, '') // Remove all other special characters
		);
	});

	const blockProps = useBlockProps({
		className: `aagb__accordion_container panel ${categoryClassList.join(' ')} 
			${step ? 'step' : ''} 
			${checkList ? 'check-list' : ''} 
			${makeActive ? 'aagb__accordion_active' : ''} 
			${QaStyle ? 'style-qa' : ''}`,
		'data-autoplay': !!autoPlay,
		'data-duration': duration,
		'data-progress-bar-direction': progressBarDirection,
		'data-feature-image-url': featureImageUrl,
        'data-auto-numbering': autoNumbering,
        'data-progress-bar-on': progressBarOn,
	});

	return (
		<>
			<div
				{...blockProps}
				tabIndex={accessibilityOn ? 0 : -1}
				id={singleAcdId}
			>
                <ProgressBar.Vertical {...props} />
				<AccordionHead {...props} />
				<AccordionBody {...props} />
                <ProgressBar.Horizontal {...props} />
			</div>
		</>
	);
}
